.ripple {
  position: absolute;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  opacity: 1;
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0); }
  .ripple.isReppling {
    -webkit-animation: ripple 0.5s linear;
            animation: ripple 0.5s linear; }

@-webkit-keyframes ripple {
  100% {
    opacity: 0;
    -webkit-transform: scale(3);
            transform: scale(3); } }

@keyframes ripple {
  100% {
    opacity: 0;
    -webkit-transform: scale(3);
            transform: scale(3); } }



